import React from "react";
import { Photo } from "../components/ImagePicker";

export interface Memory {
  id: string;
  imagePath: string;
  title: string;
  type: "good" | "bad";
  base64Url: string;
}

const MemoriesContext = React.createContext<{
  memories: Memory[];
  addMemory: (photo: Photo, title: string, type: "good" | "bad") => void;
  initContext: () => void;
}>({
  memories: [],
  addMemory: () => {},
  initContext: () => {}
});

export default MemoriesContext;
